#about {
margin-top: 30px;
padding-bottom: 30px;

@media (min-width: $tablet-breakpoint) and (max-width: 1279px) 
  { margin-top: 50px;
    padding-bottom: 50px;
  }
  @media (min-width: $desktop-breakpoint)  
  { margin-top: 50px;
    padding-bottom: 50px;
  }}
.about-image {
  width: 100%;
 
  @media (min-width: $tablet-breakpoint) and (max-width: 1279px) 
  {
 
  }

  @media (min-width: $desktop-breakpoint) {
  
  }
}

.about-image > img {
  display: flex;
  width: 100%;
  object-fit: cover;
}
@media (min-width: $mobile-breakpoint) and (max-width: 767px) {
  .about-image {min-height: 178px;
  max-height: 344px;}
  .about-wrapper {
    margin-top: 32px;
  }
  .about-description {
    margin-block-end: 0;
    margin-block-start:0 ;
    margin-bottom: 16px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
  }
}
@media (min-width: $tablet-breakpoint) and (max-width: 1279px) {
  .about-box-image {
    align-items: center;
  }
  .about-image {min-width: 344px;}
  .about-wrapper {
    
    display: flex;
    gap: 54px;
   align-items: center;}
  .about-description {
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    line-height: 24px;
    min-width: 322px;
  }

 
}
@media (min-width: $desktop-breakpoint)  {
  .about-description {
    font-size: 22px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    line-height: 27px;
    min-width:510px ;
  }
 .about-image {min-width: 632px;
max-height: 344px;}
  .about-wrapper {
    display: flex;
    gap: 90px;
    align-items: center;}
  }